import androidx.compose.runtime.CompositionLocalProvider
import com.varabyte.kobweb.core.AppGlobalsLocal
import com.varabyte.kobweb.navigation.RoutePrefix
import com.varabyte.kobweb.navigation.Router
import com.varabyte.kobweb.navigation.UpdateHistoryMode
import com.varabyte.kobweb.silk.defer.renderWithDeferred
import kotlin.Unit
import kotlinx.browser.document
import kotlinx.browser.window
import org.jetbrains.compose.web.renderComposable

public fun main(): Unit {
    RoutePrefix.set("")
    val router = Router()
    com.varabyte.kobweb.core.init.initKobweb(router) { ctx ->
        ctx.router.register("/") { com.phoenixredwolf.web.pages.HomePage() }
        ctx.router.register("/about") { com.phoenixredwolf.web.pages.About() }
        ctx.router.register("/contact") { com.phoenixredwolf.web.pages.Contact() }
        ctx.router.register("/privacy") { com.phoenixredwolf.web.pages.Privacy() }
        ctx.router.register("/services") { com.phoenixredwolf.web.pages.Services() }
        ctx.router.register("/services/cloud") { com.phoenixredwolf.web.sections.services.Cloud() }
        ctx.router.register("/services/networks") {
                com.phoenixredwolf.web.sections.services.Networks() }
        ctx.router.register("/services/software") {
                com.phoenixredwolf.web.sections.services.software.SoftwareDevelopment() }
        ctx.router.register("/services/web") {
                com.phoenixredwolf.web.sections.services.web.WebDevelopment() }

    }
    router.addRouteInterceptor {
        path = path.removeSuffix(".html").removeSuffix(".htm")
    }

    com.varabyte.kobweb.silk.init.initSilkHook = { ctx ->
        ctx.theme.registerComponentStyle(com.phoenixredwolf.web.styles.BackToTopButtonStyle)
        ctx.theme.registerComponentStyle(com.phoenixredwolf.web.styles.NavBarButtonStyle)
        ctx.theme.registerComponentStyle(com.phoenixredwolf.web.styles.InputStyle)
        ctx.theme.registerComponentStyle(com.phoenixredwolf.web.styles.MainButtonStyle)
        ctx.theme.registerComponentStyle(com.phoenixredwolf.web.styles.CardStyle)
        ctx.theme.registerComponentStyle(com.phoenixredwolf.web.styles.Card_circleStyle)
        ctx.theme.registerComponentStyle(com.phoenixredwolf.web.styles.H2Style)
        ctx.theme.registerComponentStyle(com.phoenixredwolf.web.styles.PStyle)
        ctx.theme.registerComponentStyle(com.phoenixredwolf.web.styles.CtaContainerStyle)
        ctx.theme.registerComponentStyle(com.phoenixredwolf.web.styles.CtaStyle)
        ctx.theme.registerComponentStyle(com.phoenixredwolf.web.styles.NavHeaderStyle)
        ctx.theme.registerComponentStyle(com.phoenixredwolf.web.styles.NavigationItemStyle)
        ctx.theme.registerComponentStyle(com.phoenixredwolf.web.styles.NavStyle)
        ctx.theme.registerComponentStyle(com.phoenixredwolf.web.styles.Dropdowncardstyle)
        ctx.theme.registerComponentStyle(com.phoenixredwolf.web.styles.GridStyle)
        ctx.theme.registerComponentStyle(com.phoenixredwolf.web.styles.ServicesStyle)
        ctx.theme.registerComponentStyle(com.phoenixredwolf.web.styles.SocialLinkStyle)
        ctx.theme.registerComponentStyle(com.phoenixredwolf.web.styles.LogoStyle)
        ctx.theme.registerComponentStyle(com.phoenixredwolf.web.styles.FooterItemStyle)
        ctx.theme.registerComponentStyle(com.phoenixredwolf.web.styles.SectionMenuStyle)
        ctx.theme.registerComponentStyle(com.phoenixredwolf.web.styles.IndexPageButtonStyle)
        ctx.theme.registerComponentStyle(com.phoenixredwolf.web.styles.DropDownStyle)
        ctx.theme.registerComponentStyle(com.phoenixredwolf.web.styles.BackgroundLogoStyle)
        ctx.theme.registerComponentVariants(com.phoenixredwolf.web.styles.NavButtonVariant)
        com.phoenixredwolf.web.updateTheme(ctx)
    }

    router.navigateTo(window.location.href.removePrefix(window.location.origin),
            UpdateHistoryMode.REPLACE)

    // For SEO, we may bake the contents of a page in at build time. However, we will overwrite them
    // the first time we render this page with their composable, dynamic versions. Think of this as
    // poor man's hydration :)
    // See also: https://en.wikipedia.org/wiki/Hydration_(web_development)
    val root = document.getElementById("root")!!
    while (root.firstChild != null) {
        root.removeChild(root.firstChild!!)
    }

    renderComposable(rootElementId = "root") {
        CompositionLocalProvider(
            AppGlobalsLocal provides mapOf("title" to "PhoenixRedwolf Digital Services")
        ) { com.phoenixredwolf.web.MyApp {
              router.renderActivePage { renderWithDeferred { it() } }
        } }
    }
}
